
import type { LucideIcon } from 'lucide-react';
import { Atom, Beaker, BookOpen, Brain, FlaskConical, TestTube, HelpCircle, LayoutDashboard, BookCopy, Users, LogOut, Settings } from 'lucide-react';

export const iconMap: Record<string, LucideIcon> = {
  Atom,
  Beaker,
  BookOpen,
  Brain,
  FlaskConical,
  TestTube,
  LayoutDashboard,
  BookCopy,
  Users,
  LogOut,
  Settings,
  HelpCircle,
};

export const defaultLectureIconComponent = HelpCircle;

export function getIconComponent(name?: string): LucideIcon {
  if (name && iconMap[name]) {
    return iconMap[name];
  }
  return defaultLectureIconComponent;
}

export const availableLectureIcons = [
  'Atom',
  'Beaker',
  'BookOpen',
  'Brain',
  'FlaskConical',
  'TestTube',
  'HelpCircle',
];
