
"use client"; // Must be client component to use useAuth hook

import type { ReactNode } from 'react';
import { AdminNav } from './AdminNav';
import { AppShell } from '@/components/layout/AppShell'; // Using AppShell for consistent header/footer
import { useAuth } from '@/hooks/useAuth';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import Link from 'next/link';

export default function AdminLayout({ children }: { children: ReactNode }) {
  const { authUser, isAdmin, loading } = useAuth();
  const router = useRouter();

  useEffect(() => {
    if (!loading) {
      if (!authUser) {
        router.replace('/auth/login?redirect=/admin');
      } else if (!isAdmin) {
        router.replace('/'); // Or a specific "access denied" page
      }
    }
  }, [authUser, isAdmin, loading, router]);

  if (loading) {
    return (
      <AppShell>
        <div className="flex min-h-screen items-center justify-center">
          <p className="text-lg text-muted-foreground">Loading admin area...</p>
        </div>
      </AppShell>
    );
  }

  if (!authUser || !isAdmin) {
    // This case should ideally be handled by the redirect, but as a fallback:
    return (
       <AppShell>
        <div className="flex min-h-[calc(100vh-10rem)] items-center justify-center p-4">
          <Card className="w-full max-w-md text-center shadow-lg">
            <CardHeader>
              <CardTitle className="text-2xl font-bold text-destructive">Access Denied</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-muted-foreground mb-6">
                You do not have permission to access this area.
              </p>
              <Link href={!authUser ? "/auth/login" : "/"} passHref>
                <Button variant="default">
                  {!authUser ? "Go to Login" : "Go to Homepage"}
                </Button>
              </Link>
            </CardContent>
          </Card>
        </div>
      </AppShell>
    );
  }

  return (
    <AppShell>
      <div className="flex">
        <AdminNav />
        <main className="flex-1 p-6 bg-muted/20 min-h-[calc(100vh-4rem)] overflow-y-auto">
          {children}
        </main>
      </div>
    </AppShell>
  );
}
