
"use client";

import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { FlaskConical, Home, BookOpen, Settings, LogIn, UserPlus, LogOut, ShieldCheck, UserCircle, Sun, Moon } from 'lucide-react';
import { usePathname, useRouter } from 'next/navigation';
import { cn } from '@/lib/utils';
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuLabel,
    DropdownMenuSeparator,
    DropdownMenuTrigger,
  } from "@/components/ui/dropdown-menu"
import { useUserProgress } from '@/contexts/UserProgressContext';
import { toast } from '@/hooks/use-toast';
import { useAuth } from '@/hooks/useAuth';
import { auth } from '@/lib/firebase';
import { signOut } from 'firebase/auth';
import { useTheme } from '@/components/layout/ThemeProvider';

export function Header() {
  const pathname = usePathname();
  const router = useRouter();
  const { resetProgress } = useUserProgress();
  const { authUser, isAdmin, loading } = useAuth();
  const { theme, toggleTheme } = useTheme();

  const handleLogout = async () => {
    await signOut(auth);
    toast({
        title: "Logged Out",
        description: "You have been successfully logged out.",
    });
    router.push('/');
  };

  const handleResetProgress = () => {
    resetProgress();
    toast({
        title: "Progress Reset",
        description: "Your learning progress has been reset.",
    });
  };

  return (
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-16 items-center justify-between">
        <Link href="/" className="flex items-center gap-2" aria-label="ChemLearn Home">
          <FlaskConical className="h-8 w-8 text-primary" />
          <span className="text-2xl font-bold text-foreground">ChemLearn</span>
        </Link>
        <nav className="flex items-center gap-1 sm:gap-2">
          <Link href="/" passHref>
            <Button variant={pathname === '/' ? 'default' : 'ghost'} size="sm" aria-current={pathname === '/' ? 'page' : undefined}>
              <Home className="mr-0 sm:mr-2 h-4 w-4" />
              <span className="hidden sm:inline">Home</span>
            </Button>
          </Link>
          <Link href="/lectures" passHref>
            <Button variant={pathname.startsWith('/lectures') ? 'default' : 'ghost'} size="sm" aria-current={pathname.startsWith('/lectures') ? 'page' : undefined}>
              <BookOpen className="mr-0 sm:mr-2 h-4 w-4" />
              <span className="hidden sm:inline">Lectures</span>
            </Button>
          </Link>

          {loading ? (
            <Button variant="ghost" size="sm" disabled>Loading...</Button>
          ) : authUser ? (
            <>
              {isAdmin && (
                <Link href="/admin" passHref>
                  <Button variant={pathname.startsWith('/admin') ? 'default' : 'ghost'} size="sm">
                    <ShieldCheck className="mr-0 sm:mr-2 h-4 w-4" />
                     <span className="hidden sm:inline">Admin</span>
                  </Button>
                </Link>
              )}
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" size="icon" className="rounded-full">
                    {authUser.displayName ? (
                        <div className="flex h-8 w-8 items-center justify-center rounded-full bg-primary text-primary-foreground text-sm font-semibold">
                           {authUser.displayName.charAt(0).toUpperCase()}
                        </div>
                    ) : (
                        <UserCircle className="h-6 w-6" />
                    )}
                    <span className="sr-only">User Menu</span>
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuLabel>{authUser.email}</DropdownMenuLabel>
                  <DropdownMenuSeparator />
                  {/* <Link href="/profile" passHref><DropdownMenuItem>Profile</DropdownMenuItem></Link> */}
                  <DropdownMenuItem onClick={handleResetProgress}>Reset Progress</DropdownMenuItem>
                  <DropdownMenuSeparator />
                  <DropdownMenuItem onClick={handleLogout} className="text-red-600 focus:text-red-600 focus:bg-red-50">
                    <LogOut className="mr-2 h-4 w-4" /> Logout
                  </DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            </>
          ) : (
            <>
              <Link href="/auth/login" passHref>
                <Button variant="ghost" size="sm">
                  <LogIn className="mr-0 sm:mr-2 h-4 w-4" /> 
                  <span className="hidden sm:inline">Login</span>
                </Button>
              </Link>
              <Link href="/auth/signup" passHref>
                <Button variant="default" size="sm">
                  <UserPlus className="mr-0 sm:mr-2 h-4 w-4" /> 
                  <span className="hidden sm:inline">Sign Up</span>
                </Button>
              </Link>
            </>
          )}
          
          <Button variant="ghost" size="icon" onClick={toggleTheme} aria-label="Toggle theme" className="ml-1">
            {theme === 'light' ? <Moon className="h-5 w-5" /> : <Sun className="h-5 w-5" />}
          </Button>

           <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" size="icon" className="ml-1">
                <Settings className="h-5 w-5" />
                <span className="sr-only">Settings</span>
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end">
              <DropdownMenuLabel>General Settings</DropdownMenuLabel>
              <DropdownMenuSeparator />
              <DropdownMenuItem onClick={handleResetProgress} disabled={!authUser}>
                Reset Learning Progress
              </DropdownMenuItem>
              {/* Add more settings later, e.g., theme toggle */}
            </DropdownMenuContent>
          </DropdownMenu>
        </nav>
      </div>
    </header>
  );
}
