
"use client";

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';
import { LayoutDashboard, BookCopy, Users, Settings, ChevronLeft } from 'lucide-react'; // Assuming icons exist
import { getIconComponent } from '@/lib/icons';

const navItems = [
  { href: '/admin', label: 'Dashboard', iconName: 'LayoutDashboard' },
  { href: '/admin/lectures', label: 'Lectures', iconName: 'BookCopy' },
  // { href: '/admin/users', label: 'Users', iconName: 'Users' },
  // { href: '/admin/settings', label: 'Settings', iconName: 'Settings' },
];

export function AdminNav() {
  const pathname = usePathname();

  return (
    <aside className="sticky top-16 h-[calc(100vh-4rem)] w-64 border-r bg-background flex flex-col shadow-md">
      <div className="p-4 border-b">
        <h2 className="text-xl font-semibold text-primary">Admin Panel</h2>
      </div>
      <nav className="flex-grow p-4 space-y-2">
        {navItems.map((item) => {
          const Icon = getIconComponent(item.iconName);
          const isActive = pathname === item.href || (item.href !== '/admin' && pathname.startsWith(item.href));
          return (
            <Link href={item.href} key={item.label} legacyBehavior>
              <a
                className={cn(
                  'flex items-center gap-3 rounded-md px-3 py-2.5 text-sm font-medium transition-colors',
                  isActive
                    ? 'bg-primary text-primary-foreground shadow-sm'
                    : 'hover:bg-muted hover:text-accent-foreground',
                )}
                aria-current={isActive ? 'page' : undefined}
              >
                <Icon className={cn("h-5 w-5 shrink-0", isActive ? "text-primary-foreground" : "text-muted-foreground")} />
                <span className="truncate flex-1">{item.label}</span>
              </a>
            </Link>
          );
        })}
      </nav>
       <div className="p-4 border-t">
        <Link href="/" passHref>
            <Button variant="outline" className="w-full">
                <ChevronLeft className="mr-2 h-4 w-4" /> Back to Site
            </Button>
        </Link>
      </div>
    </aside>
  );
}
